.list {
  margin: 0;
  padding: 4px;
  list-style: none;
}

.item {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.contact {
  text-transform: capitalize;
  font-weight: 400;
  font-size: 16px;
  color: #000033;
}

.button {
  width: 60px;
  padding: 6px;
  border: none;
  border-radius: 40px;
  cursor: pointer;

  text-align: center;
  font-weight: 300;
  font-size: 14px;

  background-color: #000033;
  color: #ffffff;
}
